import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        component: () => import('@/layouts/EmptyLayout.vue'),
        children: [
          {path: '', name: 'login', component: () => import('../views/Login.vue')},
        ]
    },
    {
        path: '/',
        component: () => import('@/layouts/MainLayout.vue'),
        children: [
          {path: 'home', name: 'home', component: () => import('../views/Home/Index.vue')},
          {path: 'profile', name: 'profile', component: () => import('../views/Profile/Index.vue')},
          {path: 'roles', name: 'roles', component: () => import('../views/Profile/Roles/Index.vue')},

          {path: 'finance', name: 'finance', component: () => import('../views/Finance/Index.vue')},
          {path: 'codebase', name: 'codebase', component: () => import('../views/CodeBase.vue')},

          {path: 'warehouse/ip', name: 'warehouse-ip', component: () => import('../views/Warehouse/Thread/Index.vue')},
          {path: 'warehouse/mato', name: 'warehouse-mato', component: () => import('../views/Warehouse/Material/Index.vue')},
          {path: 'warehouse/yoqa', name: 'warehouse-yoqa', component: () => import('../views/Warehouse/Collar/Index.vue')},

          {path: 'outsource/mato-toquv/:id', name: 'outsource-mato-toquv-id', component: () => import('../views/Outsource/MaterialWeaving/Index.vue')},
          {path: 'outsource/yoqa-toquv/:id', name: 'outsource-yoqa-toquv-id', component: () => import('../views/Outsource/CollarWeaving/Index.vue')},
          {path: 'outsource/boyoqxona', name: 'outsource-boyoqxona', component: () => import('../views/Outsource/Painting/Index.vue')},

          {path: 'supply/tayyor-xomashyo/mato', name: 'supply-tayyor-xomashyo-mato', component: () => import('../views/Supply/RawReady/Material/Index.vue')},
          {path: 'supply/tayyor-xomashyo/yoqa', name: 'supply-tayyor-xomashyo-yoqa', component: () => import('../views/Supply/RawReady/Collar/Index.vue')},
          {path: 'supply/aksessuar', name: 'supply-aksessuar', component: () => import('../views/Supply/Accessory/Index.vue')},
          {path: 'supply/bichuv', name: 'supply-bichuv', component: () => import('../views/Supply/Tailoring/Index.vue')},

          {path: 'factory/:id', name: 'factory-id', component: () => import('../views/Factory/Index.vue')},

          {path: 'ready-products/:id', name: 'ready-products-id', component: () => import('../views/Ready-Products/Index.vue')},
        ]
    }
];

const router = new VueRouter({
    routes,
    mode:'history'
});




